import React from 'react'
import Paragraph from './../shared/paragraph';
import PageTitle from './../shared/page-title';
import Link from './../shared/link';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text="About"/>

                <Paragraph>
                    My name is Aleksey Fedotov, I develop enterprise web apps on my daily job
                    and create games and graphics demos as a hobby. This is my personal site
                    and blog where I occasionally write on topics Iâ€™m interested in,
                    mostly programming, graphics or gamedev-related.
                </Paragraph>

                <Paragraph>
                    If you wish to drop me a line or two you can reach me
                    on <Link to="https://twitter.com/0xc0dec" newPage>Twitter</Link>,&nbsp;
                    <Link to="https://github.com/0xc0dec" newPage>Github</Link> or via <i>0xc0dec at gmail.com</i>.
                </Paragraph>
            </div>
        );
    }
}


